<template>
  <md-app md-mode="fixed">
    <md-app-content>
      <SmallTopBanner/>
      <div id="main">
        <Header />
        <div class="main-holder">
          <Breadcrumbs />
          <router-view />
        </div>
        <Footer />
      </div>
      <CookieBanner />
    </md-app-content>
  </md-app>
</template>

<script>
import { mapActions } from 'vuex';
import axios from 'axios';
import i18n from '@/i18n';

import CookieBanner from './components/CookieBanner.vue';
import SmallTopBanner from './components/SmallTopBanner.vue';
import Header from './components/Header.vue';
import Breadcrumbs from './components/Breadcrumbs.vue';
import Footer from './components/Footer.vue';


export default {
  components: {
    Header,
    Breadcrumbs,
    Footer,
    CookieBanner,
    SmallTopBanner,
  },
  data: () => ({
    menuVisible: false,
  }),
  mounted() {
    i18n.locale = 'de';
    document.title = 'Corona Antigentests einfach online bestellen | Covid123.at';
  },
  created() {
    this.checkProducts();
  },
  methods: {
    ...mapActions(['updateCartItems']),
    checkProducts() {
      if (localStorage.getItem('contextToken')) {
        axios
          .get('http://77.245.20.254/store-api/v3/checkout/cart',
            {
              headers: {
                'sw-access-key': 'SWSCAUPEWFJVU0FVC20WCNDLUG',
                'sw-context-token': localStorage.getItem('contextToken'),
              },
            })
          .then((response) => {
            this.updateCartItems(response.data.lineItems);
          })
          .catch((e) => {
            // eslint-disable-next-line no-console
            console.error('Error occured on App component while cart response ', e);
            this.$toaster.error(this.$t('notify.oops'));
          });
      }
    },
  },
  watch: {
    $route(newRoute, oldRoute) {
      if (newRoute.path !== oldRoute.path) {
        const scrolls = document.querySelectorAll('.md-scrollbar');
        scrolls.forEach((scrollItem) => {
          if (scrollItem.scrollTop) {
            // eslint-disable-next-line no-param-reassign
            scrollItem.scrollTop = 0;
          }
        });
      }
    },
  },
};
</script>

<style lang='scss'>
@import './styles/main.scss';
</style>
