<template>
  <header>
    <div
      class="mobile-header"
    >
      <md-app-toolbar>
        <div class="left-mob-menu">
          <md-menu
            md-size="medium"
            md-align-trigger
          >
            <md-button
              class="md-icon-button"
              md-menu-trigger
            >
              <md-icon>person</md-icon>
            </md-button>
            <md-menu-content class="menu-item-center radius-block radius-block_auto-height radius-block_mob">
              <md-menu-item
                v-if="isUserAuthorized"
                @click="logOut()"
              >
                {{$t('header.logOut')}}
              </md-menu-item>
              <md-menu-item
                v-if="!isUserAuthorized"
                @click="showModal()"
              >
                {{$t('header.logIn')}}
              </md-menu-item>
            </md-menu-content>
          </md-menu>
          <md-badge
            v-if="getCartItemsCount > 0"
            class="md-badge_visible"
            :md-content="getCartItemsCount"
          >
            <md-button
              class="md-icon-button mar-0"
              @click="goToBasket()"
            >
              <md-icon>shopping_cart</md-icon>
            </md-button>
          </md-badge>
        </div>
        <div class="md-toolbar-section-start">
          <router-link
            to="/"
            class="logo logo_custom md-layout-item"
          >
            <img
              src="../assets/svg-icons/covid123.at.svg"
              alt="Axonlab"
              class="logo__img"
            >
          </router-link>
        </div>
        <md-button @click="toggleMenu($event)" class="md-icon-button toggle-menu-element">
          <md-icon ref="mobile-header-menu">menu</md-icon>
        </md-button>
      </md-app-toolbar>
      <md-drawer :md-active.sync="menuVisible" md-swipeable>
        <md-toolbar md-elevation="0">
          <span>{{$t('header.navigation')}}</span>
          <div class="md-toolbar-section-end">
            <md-button @click="toggleMenu($event)" class="md-icon-button md-dense toggle-menu-element">
              <md-icon>keyboard_arrow_left</md-icon>
            </md-button>
          </div>
        </md-toolbar>
        <md-list>
          <md-list-item
            v-for="(item, index) in productsAT"
            :key="index"
            @click="onProductSelect(item)"
          >
            {{ item.title }}
          </md-list-item>
          <hr/>
          <md-list-item v-if="!isUserAuthorized">
            <span
              class="toggle-menu-element"
              @click="showModal()"
            >
              {{$t('header.logIn')}}
            </span>
          </md-list-item>
          <md-list-item v-if="isUserAuthorized">
            <span @click="logOut()">
              {{$t('header.logOut')}}
            </span>
          </md-list-item>
          <md-list-item class="mar-b-2"></md-list-item>
        </md-list>
      </md-drawer>
      <!-- mobile right menu -->
      <md-drawer
        class="md-right"
        :md-active.sync="showMobileQuickCart"
      >
        <md-toolbar
          class="md-transparent"
          md-elevation="0"
        >
          <!-- <QuickCard
            :isMobile="true"
            v-on:needShowModal="quickCartActionHandler($event)"
          /> -->
        </md-toolbar>
      </md-drawer>
    </div>
    <div class="desktop-header md-xsmall-hide">
      <div class="md-layout md-alignment-center-space-between pad-b-1 mar-b-1">
        <div class="md-layout-item left-search-side">
          <div class="md-layout md-alignment-center-left">
            <router-link
              to="/"
              class="logo md-layout-item"
            >
              <img
                src="../assets/svg-icons/covid123.at.svg"
                alt="Axonlab"
                class="logo__img"
              >
            </router-link>
          </div>
        </div>
        <div class="right-desktop-part">
          <div class="md-layout md-alignment-center-right">
            <md-button
              class="sign-in-btn radius-block hide-medium"
              v-if="!isUserAuthorized"
              @click="onLoginButtonClick()"
            >
              <md-icon class="mar-r-1">
                <img
                  src="../assets/svg-icons/sign-in-alt-solid.svg"
                  alt="signIn icon"
                >
              </md-icon>
              {{$t('header.logIn')}}
            </md-button>
            <md-button
              class="sign-in-btn round-btn hide show-medium"
              v-if="!isUserAuthorized"
              @click="onLoginButtonClick()"
            >
              <md-icon>
                <img
                  src="../assets/svg-icons/sign-in-alt-solid.svg"
                  alt="signIn icon"
                >
              </md-icon>
            </md-button>
            <md-button
              to="/user-profile"
              class="round-btn hide show-medium"
              v-if="isUserAuthorized && $route.name !== 'userProfile'"
            >
              <i class="md-icon">person</i>
            </md-button>
            <md-button
              class="radius-block hide-medium"
              v-if="isUserAuthorized"
              @click="logOut()"
            >
              <md-icon class="mar-r-1">
                <img
                  src="../assets/svg-icons/sign-out-alt-solid.svg"
                  alt="signIn icon"
                >
              </md-icon>
              {{$t('header.logOut')}}
            </md-button>
            <md-button
              class="round-btn hide show-medium"
              v-if="isUserAuthorized"
              @click="logOut()"
            >
              <md-icon>
                <img
                  src="../assets/svg-icons/sign-out-alt-solid.svg"
                  alt="signIn icon"
                >
              </md-icon>
            </md-button>
            <md-badge
              v-if="getCartItemsCount > 0 && needToShowBasket"
              class="md-badge_visible"
              :md-content="getCartItemsCount"
            >
              <md-button
                class="round-btn"
                @click="goToBasket()"
                @mouseover.stop.prevent="onHover(true)"
                @mouseleave.stop.prevent="onHover(false)"
              >
                <md-icon>shopping_cart</md-icon>
              </md-button>
            </md-badge>
            <div
              :class="{'hide': !needToShowQuickCart}"
              class="quick-cart-container"
              @mouseover.stop.prevent="onHover(true)"
              @mouseleave.stop.prevent="onHover(false)"
            >
            </div>
          </div>
        </div>
      </div>
    </div>
    <md-dialog :md-active.sync="isModalVisible">
      <AuthorizationModal v-on:changeModalState="closeModalFlag()"/>
    </md-dialog>
  </header>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import variables from '../assets/common';
import constants from '../constants';

import AuthorizationModal from '@/components/AuthorizationModal.vue';
// import QuickCard from '@/components/QuickCard.vue';

export default {
  name: 'Header',
  data: () => ({
    needToShowQuickCart: false,
    showMobileQuickCart: false,
    menuVisible: false,
    isModalVisible: false,
    searchResult: false,
    searchWord: '',
    searchTimeoutId: '',
    categoriesList: [],
    timeoutId: '',
    needToShowBasket: true,
    needToShowAllProducts: false,
    isUserAuthorized: JSON.parse(localStorage.getItem('isUserAuthorized')),
    productsAT: constants.AVAILABLE_LOCATIONS.AT.products,
  }),
  computed: {
    ...mapGetters(['getCartItemsCount']),
  },
  components: {
    AuthorizationModal,
    // QuickCard,
  },
  methods: {
    ...mapActions(['updateAuth', 'updateProductFromMenu']),
    logOut() {
      const path = '/';

      if (this.$route.path !== path) {
        this.$router.push('/');
      }

      localStorage.setItem('isUserAuthorized', false);
      this.isUserAuthorized = false;
    },
    showModal() {
      this.isModalVisible = true;
    },
    onLoginButtonClick() {
      this.showModal();
    },
    onHover(needToShow) {
      if (needToShow) {
        clearTimeout(this.timeoutId);
        this.needToShowQuickCart = needToShow;
      } else {
        this.timeoutId = setTimeout(() => {
          this.needToShowQuickCart = false;
        }, 200);
      }
    },
    closeModalFlag() {
      this.isModalVisible = false;
    },
    toggleMenu() {
      this.menuVisible = !this.menuVisible;
    },
    goToBasket() {
      this.needToShowQuickCart = false;
      this.$router.push('/basket');
    },
    openMobileBasket() {
      this.showMobileQuickCart = true;
    },
    quickCartActionHandler(data) {
      this.showMobileQuickCart = data;
    },
    buildImgUrl(urlPart) {
      return variables.imgServer + urlPart;
    },
    categoryFilter(id) {
      this.$router.push(`/categories/${id}`);
      this.menuVisible = false;
    },
    onProductSelect(product) {
      this.updateProductFromMenu(product);
      this.toggleMenu();
    },
  },
  watch: {
    $route() {
      if (this.$route.path === '/basket') {
        this.needToShowBasket = false;
      } else {
        this.needToShowBasket = true;
      }
      this.needToShowAllProducts = this.$route.path.indexOf('categories') !== -1;
    },
  },
};
</script>
