import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import(/* webpackChunkName: "productDetails" */ '../pages/ProductDetails.vue'),
    meta: {
      breadcrumb: 'productPage',
    },
  },
  {
    path: '/forgot-password/:token',
    name: 'forgotPassword',
    component: () => import(/* webpackChunkName: "forgotPassword" */ '../pages/ForgotPassword.vue'),
    meta: {
      breadcrumb: 'forgotPassword',
    },
  },
  {
    path: '/payment/finalize-transaction',
    name: 'payment',
    component: () => import('../pages/checkout/Payment.vue'),
    props: route => ({ query: route.query.q }),
  },
  {
    path: '/basket',
    name: 'basket',
    component: () => import('../pages/Basket.vue'),
    meta: {
      breadcrumb: 'basket',
    },
  },
  {
    path: '/checkout',
    name: 'checkout',
    component: () => import('../pages/checkout/Main.vue'),
    redirect: '/checkout/user-data',
    children: [
      {
        path: 'user-data',
        name: 'user-data',
        component: () => import('../pages/checkout/UserData.vue'),
      },
      {
        path: 'final',
        name: 'final',
        component: () => import('../pages/checkout/Final.vue'),
      },
      {
        path: 'address',
        name: 'address',
        component: () => import('../pages/checkout/Address.vue'),
      },
      {
        path: 'confirmation',
        name: 'confirmation',
        component: () => import('../pages/checkout/Confirmation.vue'),
      },
      {
        path: 'summary',
        name: 'summary',
        component: () => import('../pages/checkout/Summary.vue'),
      },
    ],
  },
  {
    path: '/terms-and-condition',
    name: 'terms-and-condition',
    component: () => import('../pages/TermsAndCondition.vue'),
    meta: {
      breadcrumb: 'termsAndCondition',
    },
  },
  {
    path: '/impressum',
    name: 'impressum',
    component: () => import('../pages/Impressum.vue'),
    meta: {
      breadcrumb: 'impressum',
    },
  },
  {
    path: '/about-us',
    name: 'about-us',
    component: () => import('../pages/AboutUs.vue'),
    meta: {
      breadcrumb: 'aboutUs',
    },
  },
  {
    path: '/private-policy',
    name: 'private-policy',
    component: () => import('../pages/PrivatePolicy.vue'),
    meta: {
      breadcrumb: 'privatePolicy',
    },
  },
  {
    path: '/about-offer',
    name: 'about-offer',
    component: () => import('../pages/AboutOffer.vue'),
  },
  {
    path: '/faq',
    name: 'faq',
    component: () => import('../pages/FAQ.vue'),
  },
  {
    path: '*',
    name: 'PageNotFound',
    component: () => import('../pages/PageNotFound.vue'),
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
